<template>
    <div>
        <TopButtons/>
        <div style="height: 10px;"></div>
        <a-card>
            <CheckElection :election="election"/>
            <PublicityContent v-model="form" @submit="handleSubmit"/>
        </a-card>
    </div>
</template>

<script>
    import TopButtons from '../选举列表/components/TopButtons'
    import CheckElection from '@/components/election/check'
    import PublicityContent from '@/components/checkSuffixForm/publicityContent'
    import {getPublicizedElection, updateElectionPublicityContent} from '@/api/election'

    export default {
        components: {
            TopButtons, CheckElection, PublicityContent
        },
        props: {
            id: {
                required: true,
                type: [String, Number]
            },
        },
        computed: {
            thisId() {
                return parseInt(this.id);
            }
        },
        data() {
            return {
                election: {},
                form: {publicity_content: null},
            }
        },
        methods: {
            fetch() {
                getPublicizedElection(this.thisId)
                    .then(res => {
                        this.election = res.data;
                        this.form.publicity_content = res.data.publicity_content;
                    })
            },
            handleSubmit() {
                updateElectionPublicityContent(this.thisId,this.form)
                    .then(res=>{
                        this.$message.success('操作成功');
                    })
            }
        },
        created() {
            this.fetch();
        }
    }
</script>
